import { Character } from '@/interfaces/characters';
import Image from 'next/image';

interface CharacterProps {
  data: Character[];
}
export default function Characters({ data }: CharacterProps) {
  return (
    <div className="grid grid-cols-4 gap-3">
      {data.map((c) => (
        <div
          key={c.id}
          className="bg-secondary rounded p-2 relative overflow-hidden group"
        >
          <div className="absolute scale-x-0 bottom-0 top-0 p-1 bg-accent/80 right-0 left-0 transition group-hover:scale-x-100 origin-right group-hover:origin-left">
            <h2>{c.name}</h2>
            <hr />
            <ul>
              <li>Actor: {c.actor}</li>
              <li>Wizard {c.wizard ? 'True' : 'False'}</li>
              {c.patronus && <li>Patronus {c.patronus}</li>}
              {c.alternate_names.length > 1 && (
                <li>AKA: {c.alternate_names.join(',')}</li>
              )}
            </ul>
          </div>

          <Image
            src={c.image || '/images/image-not-found.jpg'}
            alt={c.name}
            width={0}
            height={0}
            sizes="100vw"
            className="w-full h-full object-cover"
          />
        </div>
      ))}
    </div>
  );
}

